$color: rgba(237, 129, 6, 0.56);

.button {
    @apply px-5 py-2 text-[15px] font-medium leading-4 rounded-xl
    bg-gradient-to-r from-title-color to-main-to text-white transition-all;
    box-shadow: 0px 5px 4px $color;
    &[data-image="true"] {
        @apply flex items-center justify-center gap-x-2;
    }
    &[data-secondary="true"] {
        background: transparent;
        color: #373737;
        font-weight: 600;
        border: 1px solid white;
        box-shadow: 0px 3px 7px white;
        &:hover {
            box-shadow: 0px 1px 3px white;
        }    
    }
    &[data-danger="true"] {
        @apply bg-red-500 text-white font-semibold border-none;
    }
    &:hover {
        box-shadow: 0px 3px 7px $color;
    }
    &:active {
        @apply scale-90;
    }
    &:disabled {
        @apply opacity-60 cursor-not-allowed;
    }
}

.image {
    @apply w-5 h-5;
}